.btn {
  font-family: "Untitled Sans";
  color: #d7d9dd;
  position: relative;
  background: none;
  border: none;
  font-size: 1.1rem;
  padding: 0;
  -webkit-transform-origin: left;
          transform-origin: left;
  transition: all 0.75s ease; }
  .btn::before {
    content: "";
    height: 1px;
    background-color: #4f4f51;
    width: 100%;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    transition: all 0.25s ease;
    position: absolute;
    bottom: 0;
    -webkit-transform-origin: left;
            transform-origin: left; }
  .btn:hover {
    cursor: pointer;
    color: #4f4f51; }
  .btn:hover::before {
    -webkit-transform: scaleX(1);
            transform: scaleX(1); }
  .btn:hover, .btn:active, .btn:focus {
    cursor: pointer;
    color: #4f4f51;
    outline: none; }
    .btn:hover::before, .btn:active::before, .btn:focus::before {
      -webkit-transform: scaleX(1);
              transform: scaleX(1); }

